:root{
    --mainColor:#111818;
    --mainColorLight:#1a2424;
    --secondaryColor:#DB2B39;
    --textColor:#888;
}



.contact-main{
    width: 100%;
    min-height: 100vh;
    background-color: var(--mainColor);
    color: white;
}
.contact-container{
    width:100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 8%;
}
.contact-container .contact-row{
    display: flex;
    justify-content: space-between;
    max-width: 1100px;
}
.contact-row section.col{
    display: flex;
    flex-direction: column;
}
.contact-row section.left{
    flex-basis: 35%;
    min-width: 320px;
    margin-right: 60px;
    
}

.contact-row section.right{
    flex-basis: 60%;
}

section.left .contactTitle h2{
    position: relative;
    display: inline-block;
    margin-bottom: 25px;
    font-size: 2.5em;
    font-weight: 200;
}

