section.left .contactTitle p{
    font-size: 17px;
    letter-spacing: 1px;
    line-height: 1.2;
    padding-bottom: 22px;
}
section.left .contactInfo{
    margin-bottom: 16px;
    
}

.contactInfo .iconGroup{
    display: flex;
    align-items: center;
    margin: 25px 0px;
    
}

.iconGroup .icon{
    width: 45px;
    height: 45px;
    border: 2px solid dodgerblue;
    border-radius: 50%;
    margin-right: 20px;
    position: relative;
    
}

.iconGroup .icon .fa-solid {
    font-size: 20px;
    color: #ddd;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.iconGroup .details span{
    display: block;
    color: #888;
    font-size: 18px;
}
.iconGroup .details span:nth-child(1){
    text-transform: uppercase;
    color: #ccc;
}

section.left .socialMedia{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin: 22px 0px 20px;
}
.socialMedia a{
    width: 35px;
    height: 35px;
    text-decoration: none;
    text-align: center;
    margin-right: 15px;
    border-radius: 5px;
    background-color: dodgerblue;
    transition: 0.4s;
    position: relative;
}

.socialMedia > a > .fa-brands{
    color: #ddd;
    font-size: 18px;
    line-height: 35px;
    border: 1px solid transparent;
    transition:0.4s ease-in-out;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
}


.socialMedia a:hover{
    transform: translateY(-5px);
    background-color: #2e2e2e;
    color: dodgerblue;
    border: 1px solid dodgerblue;
}
.socialMedia a:hover .fa-brands{
    color: dodgerblue;
}



.contact-row section.right .messageForm{
    display: flex;
    justify-content: space-between;
    flex-wrap:wrap ;
    padding-top: 30px;

}

.contact-row section.right .inputGroup{
    margin: 18px 0;
    position: relative;

}

.messageForm .halfWidth{
    flex-basis: 48%;
}

.messageForm .fullWidth{
    flex-basis: 100%;
}

.messageForm input, .messageForm textarea

{
    width: 100%;
    font-size: 18px;
    padding: 2px 0px;
    background-color: var(--mainColor) !important;
    color: #ddd;
    border: none;
    border-bottom: 2px solid #666;
    outline: none;
}
.messageForm input:-webkit-autofill,
.messageForm input:-webkit-autofill:hover, 
.messageForm input:-webkit-autofill:focus,
.messageForm textarea:-webkit-autofill,
.messageForm textarea:-webkit-autofill:hover,
.messageForm textarea:-webkit-autofill:focus{
    
    -webkit-text-fill-color: white;
    -webkit-text-stroke-color: white;
    caret-color: white;
    -webkit-box-shadow: 0 0 0px 1px var(--mainColor) inset;
    transition: background-color 5000s ease-in-out 0s;
    -webkit-animation-fill-mode: both;
}

.messageForm textarea{
    resize: none;
    height: 220px;
    display: block;
}
textarea::-webkit-scrollbar{
    width: 5px;
}
textarea::-webkit-scrollbar-track{
    background-color: #1e1e1e;
    border-radius: 15px;
}
textarea::-webkit-scrollbar-thumb {
    background-color: dodgerblue;
    border-radius: 15px;
}

.inputGroup label{
    position: absolute;
    left: 0;
    bottom: 4px;
    color:#888;
    font-size: 18px;
    transition: 0.4s;
    pointer-events: none;
}
.inputGroup:nth-child(4) label{
    top: 2px;
}
.inputGroup input:focus ~ label, .inputGroup textarea:focus ~label,
.inputGroup input:valid ~ label, .inputGroup textarea:valid ~label
{
    transform: translateY(-30px);
    font-size: 16px;
}

.inputGroup button{
    padding: 8px 16px;
    font-size: 18px;
    background-color: dodgerblue;
    color: #ddd;
    border: 1px solid transparent;
    border-radius: 25px;
    outline: none;
    cursor: pointer;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
    transition: 0.4s;
    
}
.inputGroup-button{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.inputGroup-button a{
    text-decoration: none;
    color: white;
    padding: 8px 16px;
    font-size: 18px;
    background-color: dodgerblue;
    color: #ddd;
    border: 1px solid transparent;
    border-radius: 25px;
    outline: none;
    cursor: pointer;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
    transition: 0.4s;
}
.inputGroup button:hover, .inputGroup a:hover{
    background-color: var(--mainColor);
    color: dodgerblue;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
    border: 1px solid dodgerblue;
}

@media(max-width:1100px){
    .messageForm .halfWidth{
        flex-basis: 100%;
    }
 }
@media(max-width:900px){
    .contact-container .contact-row{
        flex-wrap: wrap;
        margin-top: 50px;
        
    }
    .contact-row section.left, .contact-row section.right{
        flex-basis: 100%;
        margin: 0px ;
    }
 }
